import React from "react";
import PropTypes from "prop-types";
import {
  Panel,
  PanelHeader,
  Group,
  Header,
  PanelHeaderButton,
  SimpleCell,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

import Icon28GhostOutline from "@vkontakte/icons/dist/28/ghost_outline";
import Icon24Attachments from "@vkontakte/icons/dist/24/attachments";
import Icon24Settings from "@vkontakte/icons/dist/24/settings";
import Icon24Gallery from "@vkontakte/icons/dist/24/gallery";
import Icon24FavoriteOutline from "@vkontakte/icons/dist/24/favorite_outline";
import Icon28WriteOutline from "@vkontakte/icons/dist/28/write_outline";

const Home = ({ id, go }) => (
  <Panel id={id}>
    <PanelHeader
      left={
        <PanelHeaderButton onClick={go} data-to="settings">
          <Icon24Settings />
        </PanelHeaderButton>
      }
    >
      Каталог скинов
    </PanelHeader>
    {/* <Group title="Группа">
		<Cell before={<Avatar src="https://sun9-40.userapi.com/c855032/v855032297/ea70/sfIJ0RABti0.jpg" size={64} />}
          size="l" description="Сообщество игры" bottomContent={
                      <Button disabled onClick={bridge.send("VKWebAppJoinGroup", {"group_id": 95380950})} size="m">Подписаться</Button>
                  } >Nebulous</Cell>
		</Group> */}
    <Group header={<Header mode="secondary">Выберите категорию</Header>}>
      <SimpleCell
        expandable
        onClick={go}
        data-to="def_skins"
        before={<Icon24Gallery />}
      >
        Обычные скины
      </SimpleCell>
      <SimpleCell
        expandable
        onClick={go}
        data-to="pets"
        before={<Icon28GhostOutline width={24} height={24} />}
      >
        Питомцы
      </SimpleCell>
      <SimpleCell expandable disabled before={<Icon24FavoriteOutline />}>
        Частицы
      </SimpleCell>
      <SimpleCell
        expandable
        onClick={go}
        data-to="back_theme"
        before={<Icon24Attachments />}
      >
        Собственный фон
      </SimpleCell>
    </Group>
    <Group header={<Header mode="secondary">Другое</Header>}>
      <SimpleCell
        expandable
        onClick={go}
        data-to="add_skin"
        before={<Icon28WriteOutline width={24} height={24} />}
      >
        Предложить скин
      </SimpleCell>
    </Group>
  </Panel>
);

Home.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
};

export default Home;
